@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5; }

.nav {
  padding: 4px 5% 0px 5%;
  display: grid;
  grid-template-columns: 10fr 9fr 10fr;
  grid-column-gap: 0px;
  height: 80px;
  width: 100%;
  font-size: 30px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  background: #fff;
  border-bottom: 2px solid rgba(0, 0, 0, 0.055); }
  .nav h1 {
    font-size: 25px;
    line-height: 0.9; }
  .nav h2 {
    font-size: 20px;
    font-weight: 400;
    line-height: 0.9; }
  .nav .besedilapesmi {
    margin-top: 10px; }
  .nav .akordichords {
    display: flex; }
  .nav .chords {
    margin-top: 4px;
    margin-left: 2px; }

.logo {
  background-image: url("/static/notes.png");
  background-repeat: no-repeat;
  background-position-x: 167px;
  background-position-y: 33px;
  background-size: 92px; }

.searchbar {
  display: grid;
  grid-template-columns: 10fr 1fr;
  margin: 10px 0px 16px 0px;
  line-height: 1;
  border: 4px solid rgba(128, 128, 128, 0.76);
  border-radius: 3px;
  background-color: rgba(0, 0, 0, 0.018);
  color: rgba(44, 42, 42, 0.877);
  font-size: 24px;
  align-content: center; }
  .searchbar input {
    width: 100%;
    /* force to expand to container's width */
    border: none;
    background-color: transparent;
    font-size: 24px;
    padding: 5px 5px 2px 6px; }
  .searchbar input::placeholder {
    color: gray;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
    font-weight: 300; }
  .searchbar input:focus {
    outline: none; }
  .searchbar .searchicon {
    margin-top: 3px;
    text-align: center; }
    .searchbar .searchicon img {
      height: 29px;
      opacity: 0.6; }
    .searchbar .searchicon img:hover {
      opacity: 1;
      cursor: pointer; }

.right_menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-align: right;
  margin-bottom: 13px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.2; }
  .right_menu a {
    text-decoration: none;
    color: #a58977f3; }
