@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  background-color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5; }

.views {
  display: flex;
  flex-direction: row;
  font-size: 15px;
  font-weight: 300;
  color: #88949e;
  margin-bottom: 10px;
  line-height: 1; }

.item {
  background-color: transparent; }

.cardwrapper {
  border-radius: 3px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
  transition: 0.4s ease-in-out; }

.cardwrapper:hover {
  transform: translateY(-2px);
  box-shadow: 6px 6px 30px rgba(0, 0, 0, 0.3);
  cursor: pointer; }
  .cardwrapper:hover .title {
    color: #fdcfb0; }

.cardlink {
  text-decoration: none; }

.cardimagediv {
  background-repeat: no-repeat;
  background-size: 100% auto;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px; }

.cardimage {
  width: 100%;
  visibility: hidden; }

.carddata {
  margin: 0px 14px 14px 12px;
  display: grid; }
  .carddata .title {
    font-weight: 700;
    font-size: 22px;
    color: #4e5860;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    line-height: 1;
    margin-bottom: 4px; }
  .carddata .author {
    color: #74828e;
    font-size: 18px;
    font-weight: 500;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    line-height: 1;
    margin-bottom: 6px; }
